export function fordealShare(
  content,
  url,
  imageUrl,
  auto = 1,
  channel,
  titleImg,
  titleImgHeight = 70
) {
  // auto=1自动调起 =0 不自动调起
  const Content = content ? `content=${encodeURIComponent(content)}` : "";
  const Url = url ? `&url=${encodeURIComponent(url)}` : "";
  const ImageUrl = imageUrl ? `&imageUrl=${encodeURIComponent(imageUrl)}` : "";
  const Auto = `&auto=${auto}`;
  const Channel = channel ? `&channel=${encodeURIComponent(channel)}` : "";
  const TitleImg = titleImg ? `&titleImg=${encodeURIComponent(titleImg)}` : "";
  const TitleImgHeight = titleImgHeight ? `&titleImgHeight=${titleImgHeight}` : "";
  const URL = `fordeal://webshare?${Content}${Url}${ImageUrl}${Auto}${Channel}${TitleImg}${TitleImgHeight}`;
  jumpByIframe(URL);
}

export function jumpByIframe(url) {
  let iFrame;
  iFrame = document.createElement("iframe");
  iFrame.setAttribute("src", url);
  iFrame.setAttribute("style", "display:none;");
  iFrame.setAttribute("height", "1px");
  iFrame.setAttribute("width", "1px");
  document.body.appendChild(iFrame);
  return new Promise(resolve => {
    setTimeout(() => {
      iFrame && iFrame.parentNode && iFrame.parentNode.removeChild(iFrame);
      iFrame = null;
      resolve();
    }, 500);
  });
}